

export default class Jikan
{
    private _index:number;
    private _name:string;

    public static readonly KOU = new Jikan(0,"");
    public static readonly OTU = new Jikan(1,"");
    public static readonly HEI = new Jikan(2,"");
    public static readonly TEI = new Jikan(3,"");
    public static readonly BO = new Jikan(4,"");
    public static readonly KI = new Jikan(5,"");
    public static readonly KANO = new Jikan(6,"");
    public static readonly SIN = new Jikan(7,"");
    public static readonly JIN = new Jikan(8,"");
    public static readonly MIZ = new Jikan(9,"");

    public static readonly JIKANS = new Array<Jikan>(
        Jikan.KOU,
        Jikan.OTU,
        Jikan.HEI,
        Jikan.TEI,
        Jikan.BO,
        Jikan.KI,
        Jikan.KANO,
        Jikan.SIN,
        Jikan.JIN,
        Jikan.MIZ,        
    );

    private constructor(index:number,name:string)
    {
        this._index = index;
        this._name = name;    
    }

    get index():number
    {
        return this._index;
    }    

    get name():string
    {
        return this._name;        
    }    

    public static of(index:number):Jikan
    {
        return Jikan.JIKANS[index];
    }
}