
export default class Houi
{
    private _index:number;
    private _name:string;

    public static readonly NORTH = new Houi(0,"");
    public static readonly NORTH_EAST = new Houi(1,"");
    public static readonly EAST = new Houi(2,"");
    public static readonly SOUTH_EAST = new Houi(3,"");
    public static readonly SOUTH = new Houi(4,"");
    public static readonly SOUTH_WEST = new Houi(5,"");
    public static readonly WEST = new Houi(6,"");
    public static readonly NORTH_WEST = new Houi(7,"");

    public static readonly CHUUOU = new Houi(-1,"");    

    public static readonly HOUIS = new Array<Houi>(
        Houi.NORTH,
        Houi.NORTH_EAST,
        Houi.EAST,
        Houi.SOUTH_EAST,
        Houi.SOUTH,
        Houi.SOUTH_WEST,
        Houi.WEST,
        Houi.NORTH_WEST,
    );

    public static readonly TENDO = [
        Houi.SOUTH,      //2 0
        Houi.SOUTH_WEST, //3 1
        Houi.NORTH,      //4 2
        Houi.WEST,       //5 3
        Houi.NORTH_WEST, //6 4
        Houi.EAST,       //7 5
        Houi.NORTH,      //8 6
        Houi.NORTH_EAST, //9 7
        Houi.SOUTH,	     //10 8
        Houi.EAST,	    //11 9
        Houi.SOUTH_EAST, //12 10    
        Houi.WEST	    //13 11             
    ];

    private constructor(index:number,name:string)    
    {
        this._index = index;
        this._name = name;    
    }

    get index()
    {
        return this._index;    
    }    

    get name()
    {
        return this._name;    
    }    

    public rev():Houi
    {
        if(this._index === Houi.CHUUOU._index)    
        {
            return this;    
        }
        else
        {
            return Houi.of((this._index + 4) % 8);
        }
    }

    public static of(index:number):Houi
    {
        return Houi.HOUIS[index];
    }
}